import React from 'react'
import ph from '../pages/img/partnerh.png'

import '../../App.css'
import emailjs from 'emailjs-com'


import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import cont from '../pages/img/contact.png'
import ad from '../pages/img/address1.jpg'

export default function Patner() {
    const notify = () => toast("Sending....");
    
    function sendEmail(e) {
      e.preventDefault();
  
      emailjs.sendForm('gmail', 'template_fnb2qpb', e.target, 'user_Laopcfcr7Jrs4mhYOGItr')
        .then((result) => {
          console.log(result.text);
          toast.success('Send Successfull', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }, (error) => {
          console.log(error.text);
          toast.error(error.text);
          toast.warn('Error', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        });
      e.target.reset()
  
    }
    return (
        <>
    
      
      
      <section className="page-section" id="contact">
      
 
                                 
                                <img src={ph}  class="image-fluid"alt="home.jpg"
                                width="100%"/>
                                   



        <div className="container-fluid">
          <div className="text-center">
            <h2 className="section-heading text-uppercase" style={{padding:"2% 0px 0px 0px"}}><strong>Partner With Us</strong></h2>
            <h3 className="section-subheading text-muted" style={{padding:"0px 0px  7px 0px"}}>Tell us a bit about Yourself</h3>
          </div>
          <form id="contactForm" name="sentMessage" onSubmit={sendEmail}>
            <div className="row align-items-stretch mb-5">
            <div className="col-md-6">
              <div className="container-fluid">
                <div className="form-group">
                  <input className="form-control" id="title" type="text" placeholder="Title" required="required" data-validation-required-message="Please enter your Title." name="title" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                </div>
                <div className="col-md-6">
                <div className="container-fluid">
                <div className="form-group">
                  <input className="form-control" id="name" type="text" placeholder="Name" required="required" data-validation-required-message="Please enter your name." name="name" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                </div>

                <div className="col-md-6">
                <div className="container-fluid">
                <div className="form-group">
                  <input className="form-control" id="email" type="email" placeholder="Email" required="required" data-validation-required-message="Please enter your email address." name="email" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                </div>
                <div className="col-md-6">
                <div className="container-fluid">
                <div className="form-group mb-md-0">
                  <input className="form-control" id="phone" type="tel" placeholder="Phone" required="required" data-validation-required-message="Please enter your phone number." name="phone" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                </div>
                <div className="col-md-6">
                <div className="container-fluid">
                <div className="form-group">
                  <input className="form-control" id="designation" type="text" placeholder="Designation" required="required" data-validation-required-message="Please enter your  designation." name="designation" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                </div>
                <div className="col-md-6">
                <div className="container-fluid">
                <div className="form-group mb-md-0">
                  <input className="form-control" id="companywebsite" type="text" placeholder="Company Website" required="required" data-validation-required-message="Please enter your company website." name="companywebsite" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                </div>

              <div className="container-fluid"> 
              <div className="row align-items-stretch mb-5" style={{padding:"0% 1.5% 1% 1.5%"}}> 
            
              
                <div className="form-group mb-md-0">
                  <input className="form-control" id="companyad" type="text" placeholder="Company Address" required="required" data-validation-required-message="Please enter your company address." name="companyad" />
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                
                </div>


                <div className="container-fluid">
                <div className="row align-items-stretch mb-5"style={{padding:"0% 1.5% 1% 1.5%"}}> 
              
              
                <div className="form-group form-group-textarea mb-md-0">
               
                  <textarea className="form-control" id="message" placeholder="Message" required="required" data-validation-required-message="Please enter a message." name="message"></textarea>
                  <p className="help-block text-danger"></p>
                </div>
                </div>
                
             </div>
              
            </div>
            <div className="container-fluid">
            <div className="text-center"  >
           
              <div id="success" ></div>
              <div >
              <div className="container-fluid">
              <button className="btn btn-primary btn-xl text-uppercase" id="sendMessageButton" type="submit" onClick={notify} >Send Message</button>
               <div style={{padding: "2% 0 0 2%"}}>
                 </div>

               </div>
               <ToastContainer/>
               </div>
            </div>
            </div>
          </form>
        </div>
       
      </section>



      </>
    )
}


