import React, { useState, Component } from 'react'
import pipe1 from './img/ROV2.png'




export default class Tooglepp extends Component {

    state = {
        on: false,
    }
    toggle = () => {
        this.setState({
            on: !this.state.on
        })
    }
    render() {

        return (
            <div>
                {
                    this.state.on && (
                        <div className="row mb-2">


                            <div >
                                <div  className="ml-2" style={{textAlign:"left"}}>
                                    <ul>
                                        <li>Seawater Intake/outfall pipeline inspection</li>
                                        <li>Thermal Plant Tank Inspection</li>
                                        <li>Water tank inspection</li>
                                        <li>Water Sump inspection</li>
                                      
                                    </ul>



                                </div>
                                <div class="col-auto d-none d-lg-block">
                                    <div>
                                        <p>

                                        </p>
                                    </div>
                                </div>
                            </div>






                        </div>
                    )
                }
                <button type="button" class="btn btn-danger" onClick={this.toggle}>More Details</button>

            </div>
        )
    }
}
